<template>
  <Card>
    <div class="alert_login" v-show="true">
      <div style="font-size: 30px; text-align: center"></div>
      <Input v-model="model.username" placeholder="" style="margin-bottom: 10px" width="80%"></Input>
      <Input v-model="model.password" placeholder=""></Input>
      <Button @click="login" style="margin-top: 10px; width:100%"></Button>
    </div>
    <div class="alert_register" v-show="false">
      <div style="font-size: 30px"></div>
      <Input v-model="model.username" placeholder="" style="margin-bottom: 10px"></Input>
      <Input v-model="model.password" placeholder="" type="password" style="margin-bottom: 10px"></Input>
      <Input v-model="model.email" placeholder=""></Input>
      <Button @click="register" style="margin-top: 10px"></Button>
    </div>
  </Card>
</template>

<script>
import Cookies from 'js-cookie'
import { Card, Input, Button } from 'element-ui'
export default {
  data () {
    return {
      isShowLogin: true,
      model: {
        username: '',
        password: '',
        email: ''
      }
    }
  },
  methods: {
    async login () {
      const { username, password } = this.model
      if (!username || !password) {
        return this.$message.error({
          type: 'fail',
          message: ''
        })
      }
      const res = await this.$http.post('/users/api/login', this.model)
      console.log(res)

      if (res.data.code === 0) {
        // cookie
        const userid = res.data.data._id
        Cookies.set('userid', userid, { expires: 7 })
        this.isShowLogin = !this.isShowLogin
        this.$message({
          type: 'success',
          message: ''
        })
        this.$store.commit('updateLogin', true)
        this.$router.push('/maincenter/1')
      } else {
        this.$message({
          type: 'error',
          message: res.data.message
        })
      }
    },
    async register () {
      const { username, password, email } = this.model
      if (!username || !password || !email) {
        return this.$message.error({
          type: 'fail',
          message: ''
        })
      }
      const res = await this.$http.post('/users/api/register', {
        username,
        password,
        email
      })
      if (res.data.code === 0) {
        console.log(res)
        const userid = res.data.data.id
        Cookies.set('userid', userid, { expires: 7 })
        this.$message({
          type: 'success',
          message: ''
        })
        this.isShowRegister = false
        this.$store.commit('updateLogin', true)
      } else {
        this.$message({
          type: 'fail',
          message: res.data.msg
        })
      }
    }
  },
  components: {
    Card,
    Button,
    Input
  }
}
</script>

